} */
.head{
width:300px;
height: 350px;
/* border: 2px solid grey; */
border-radius: 15px;
margin-bottom: 25px;
background-color: whitesmoke;
box-shadow: 2px 2px 5px grey;
transition: 1s;
float: right;
margin-right: 140px;
}
.head:hover{
  transform: scale(1.1);
}
img{
  width: 300px; 
  height: 250px;
  border-radius: 15px;
}
button{
  width: 100px;
  height: 30px;
  border-radius: 5px;
  font-size: 15px;
  margin-top: 10px;
  font-weight: bold;
  transition: 1s;
}
#container{
  margin-left: 40px;
}
.title{
  font-size: 20px;
  color: gray;
}
.name{
  font-weight: bold;
  font-size: 25px;
}
button:hover{
  background-color: black;
  color: white;
  font-weight: bold;
}
h1{
  text-align: center;
  text-shadow: 2px 2px 4px grey;
  font-size: 40px;
  text-decoration: underline;
}